<template>
  <div>
    <div class="information-product flex">
      <div class="w-1/3">
        <img :src="'./assets/images/products/detail/gao-thom.jpg'" alt="" />
      </div>
      <div class="w-2/3">
        <p class="text-[#EE4D2D] text-2xl font-bold mb-4">Go ST21 A An</p>
        <div class="rates flex items-center w-full gap-4 my-3">
          <div><Rate></Rate></div>
          <div><span class="text-lg border-l-2 pl-4 h-5">3 nh gi</span></div>
          <div><span class="text-lg border-l-2 pl-4 h-5">3  bn</span></div>
        </div>
        <p class="text-lg text-gray-600 my-2">Hn s dng 6 thng</p>
        <span class="font-bold text-2xl text-[#EE4D2D]">10000</span>
        <div class="flex items-center h-10 gap-2">
          <p>S lng:</p>
          <div class="flex items-center h-10 gap-2">
            <button @click="overMin()">
              <Minus></Minus>
            </button>

            <span class="w-6 text-center">{{ countProduct }}</span>
            <button @click="overMax()">
              <Add></Add>
            </button>
            <span>{{ maxProduct }} Sn phm c sn</span>
          </div>
        </div>
        <div class="flex gap-2 my-2">
          <vs-button warn gradient> <span>Thm vo gi hng</span> </vs-button>
          <vs-button warn gradient> <span>Mua ngay</span> </vs-button>
        </div>
      </div>
      <Alert @closeAlert="resetNotification()" :content="content"> </Alert>
    </div>
    <div class="my-5">
      <h3 class="text-[#EE4D2D] py-2 font-bold text-2xl">Sn phm lin quan</h3>
      <MutilCarousel :showItem="4"></MutilCarousel>
    </div>
    <div class="my-5">
      <h3 class="text-[#EE4D2D] py-2 font-bold text-2xl">Thng tin sn phm</h3>
      <p class="description my-3 text-justify text-[#EE4D2D]">
        Go thm A An ST21 ti 5kg c thu hoch t ging la ST21 t nhin.
        Go ca thng hiu A An c sn xut trn dy chuyn hin i, cam kt
        khng u trn, khng cht to mi, mang li sn phm go cht lng, an
        ton cho sc kho ngi dng
      </p>
      <div>
        <div
          class="flex gap-5 text-[#EE4D2D]"
          v-for="({ content, characteristic }, index) in descriptions"
          :key="index"
        >
          <div class="characteristic font-semibold text-md min-w-[100px]">
            {{ characteristic }}
          </div>
          <div class="content">{{ content }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MutilCarousel from "../../../components/mutil-carousel/index.vue";
import Alert from "../../../components/alert/index.vue";
import Rate from "../../../components/rate/index.vue";
import Add from "../../../components/icons/add.vue";
import Minus from "../../../components/icons/minus.vue";
export default {
  data: () => ({
    countProduct: 0,
    maxProduct: 20,
    descriptions: [
      {
        characteristic: "c tnh",
        content: "Do nhiu, cm mm",
      },
      {
        characteristic: " n",
        content: "N t",
      },
      {
        characteristic: "Ging la",
        content: "Ging la ST21",
      },
      {
        characteristic: "Canh tc",
        content:
          "Theo quy trnh chm sc khoa hc, khng cht kch thch tng trng",
      },
      {
        characteristic: "Cch nu",
        content: "1 chn go cho 1 - 1,2 chn nc (tng gim tu khu v)",
      },
      {
        characteristic: "Bo qun",
        content: " ni kh ro, thong mt, trnh nh nng trc tip",
      },
      {
        characteristic: "Trng lng",
        content: "5kg",
      },
      {
        characteristic: "Thng hiu",
        content: "A An",
      },
      {
        characteristic: "Sn xut ti",
        content: "Vit Nam",
      },
    ],
    content: {
      active: false,
      notification: "",
    },
  }),
  components: {
    Rate,
    Add,
    Minus,
    Alert,
    MutilCarousel,
  },
  methods: {
    overMax() {
      if (this.countProduct < this.maxProduct) {
        this.countProduct++;
        this.content.active = false;
      } else {
        this.content.notification = "qu nhiu sn phm";
        this.content.active = true;
      }
    },
    overMin() {
      if (this.countProduct) {
        this.countProduct--;
        this.content.active = false;
      } else {
        this.content.notification = "nh nht 0 sn phm";
        this.content.active = true;
      }
    },
    resetNotification() {
      this.content.active = false;
      this.content.notification = "";
    },
  },
};
</script>

